import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {PagesComponent} from '../../pages.component';
import {BlankComponent} from '../../blank/blank.component';
import {SearchComponent} from '../../search/search.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {MinsitryParastatalComponent} from './minsitry-parastatal/minsitry-parastatal.component';
import {CreateParastatalComponent} from './create-parastatal/create-parastatal.component';
import {CustomErrorModule} from '../../../shared/custom-error/custom-error.module';
import {LoaderModule} from '../../../shared/loader/loader.module';

export const routes: Routes = [
    {path: ':id', component: MinsitryParastatalComponent},
];

@NgModule({
    declarations: [MinsitryParastatalComponent, CreateParastatalComponent],
    imports: [
        CommonModule,
        PerfectScrollbarModule,
        RouterModule.forChild(routes),
        FormsModule, CustomErrorModule, LoaderModule,
        ReactiveFormsModule
    ]
})
export class ParastatalModule {
}
