import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import Swal from 'sweetalert2';
import {Constants} from '../../../../app.constants';
import {ActivatedRoute, Router} from '@angular/router';
import {MinistryManagementService} from '../../../../services/ministry-management.service';
import {FormGroup} from '@angular/forms';
import {IMinistry, IState} from '../../../../interfaces/interfaces';

@Component({
    selector: 'app-minsitry-parastatal',
    templateUrl: './minsitry-parastatal.component.html',
    styleUrls: ['./minsitry-parastatal.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class MinsitryParastatalComponent implements OnInit {

    public NewMinistryForm: FormGroup;
    errorMessage = '';
    loading: boolean;
    pageLoading: boolean;
    onSuccess: any;
    showUpdate: boolean;
    public cities: any;
    public states: IState[];
    institution_id: number;
    selectedForEdit = false;

    ministry: IMinistry = {
        name: '',
        code: '',
        email: '',
        phone: '',
        address: {
            address_line_1: '',
            address_line_2: '',
            state: '',
            city: ''
        },
        pobox_number: '',
        created_by: '',
        updated_by: '',
        created_at: '',
        updated_at: '',
    }


    constructor(private ministryManagementService: MinistryManagementService,
                private route: ActivatedRoute,
                private router: Router) {
        this.showUpdate = false;
    }

    ngOnInit() {
        const id = this.route.snapshot.params['id'];
        this.getMinistryById(id);
        this.institution_id = id;
    }


    getMinistryById(id: number) {
        this.pageLoading = true;
        this.errorMessage = '';
        this.ministryManagementService.getMinistryByID(id).subscribe((ministry: any) => {
            this.ministry = ministry;
            this.pageLoading = false
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
            this.pageLoading = false
        });
    }

    editParastatal(parastatal) {

    }

    deleteParastatal(id, i) {
        Swal.fire({
            title: 'Confirmation',
            text: 'Do you want to delete this parastatal?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.ministryManagementService.deleteParastatal(id).subscribe(deleted => {
                    Swal.fire('Deleted!', 'Institution has been deleted!', 'success');
                    this.ministry.parastatals.splice(i, 1);
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                })
            }
        });
    }

}
