import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ICity, IMinistry, IParastatal, IState} from '../../../../interfaces/interfaces';
import {MinistryManagementService} from '../../../../services/ministry-management.service';
import Swal from 'sweetalert2';
import {Constants} from '../../../../app.constants';
import {Router} from '@angular/router';

@Component({
    selector: 'app-create-parastatal',
    templateUrl: './create-parastatal.component.html',
    styleUrls: ['./create-parastatal.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CreateParastatalComponent implements OnInit {

    public NewParastatalForm: FormGroup;
    errorMessage: any[];
    loading: boolean;
    states: IState[];
    cities: ICity[] = [];
    @Input() institution_id: number;
    @Input() editData: any = false;
    public editMode = false;

    constructor(private formBuilder: FormBuilder,
                private ministryManagementService: MinistryManagementService,
                private router: Router) {
        if (this.editData) {
            this.editMode = true;
        }
    }

    ngOnInit() {
        this.buildForm();
        this.getStates();
    }

    buildForm() {
        this.NewParastatalForm = this.formBuilder.group({
            name: [this.editData ? this.editData.name : '', Validators.compose([Validators.required, Validators.maxLength(255)])],
            email: [this.editData ? this.editData.email : '', Validators.compose([Validators.required, Validators.email])],
            phone: [this.editData ? this.editData.phone : '', Validators.compose(
                [Validators.required, Validators.minLength(8), Validators.maxLength(20)])],
            institution_id: [this.institution_id, Validators.required],
            state_id: [(this.editData && this.editData.address) ? this.editData.address.state_id : '', Validators.required],
            city_id: [(this.editData && this.editData.address) ? this.editData.address.city_id : '',
                Validators.compose([Validators.required])],
            address_line_1: [(this.editData && this.editData.address) ? this.editData.address.address_line_1 : '', Validators.required],
            address_line_2: [(this.editData && this.editData.address) ? this.editData.address.address_line_2 : ''],
            pobox_number: [(this.editData && this.editData.address) ? this.editData.address.pobox_number : ''],
        })
    }

    changeCities(value) {
        this.cities = [];
        if (value) {
            for (let x = 0; x < this.states.length; x++) {
                if (this.states[x].id.toString() === value.toString()) {
                    this.cities = this.states[x].cities;
                    break;
                }
            }
        }
    }

    public getStates(): void {
        this.ministryManagementService.getStates().subscribe((states: IState[]) => {
                this.states = states;
            }
        );
    }

    public onSubmit(value: IMinistry): void {
        this.loading = true;

        let subscriber = this.ministryManagementService.addParastatal(value);

        if (this.editMode) {
            subscriber = this.ministryManagementService.updateParastatal(this.editData.id, value);
        }

        subscriber.subscribe((parastatal: any) => {
            if (parastatal) {
                document.getElementById('toggleCreateParastatal').click();
                Swal.fire('Created!', 'New parastatal created successfully!', 'success');
            }
            this.loading = false;
        }, (err) => {
            this.loading = false;
            Constants.handleError(err, this.router, true);
        })
    }

}
